<template>
  <!--혜택목록, 지점 혜택 목록-->
  <div class="swiperWrap swiperDivi swipeType05" data-slide-per-view="auto">
    <div class="swiper-container">
      <button type="button" class="swiper-button-prev">이전</button>
      <ul class="swiper-wrapper">
        <li v-for="(item, index) in parsedData" v-bind:key="index" class="swiper-slide ">
          <div>
            <a @click="click('이동', item)" >
              <span class="swImgs"><img :src="item.imgUrl" alt=""/></span>
              <span class="swFlag">
                <span class="flagHis" v-bind:class="color(item)">{{item.eventGbnNm}}</span>
                <!-- <span class="flagHis blueF02">{{item.eventGbnNm}}</span> -->
<!--                <span class="flagHis redF02">사은</span>-->
<!--                <span class="flagHis neonF">제휴</span>-->
<!--                <span class="flagHis grayF">결제</span>-->
<!--                <span class="flagHis redF">명동점</span>-->
              </span>
              <span class="swInfo">
                <span class="tit">{{item.title}}</span>
                <span class="subTit">{{item.subTitle}}</span>
                <span class="date">{{item.eventStartDate}}</span>
              </span>
            </a>
          </div>
        </li>
        <!--캐러셀 항목 5개 이상일시에만 노출-->
        <li v-if="data.list.length > 5 && show_more" class="swiper-slide btnMors">
          <button @click="click('더보기')" :class="isOnConsulting ? 'cb-disabled-button' : ''"  type="button" class="btMore">{{ $t("common.더보기") }}</button>
        </li>
      </ul>
      <button type="button" class="swiper-button-next">다음</button>
    </div>
  </div>
  <!--//혜택목록, 지점 혜택 목록-->
</template>
<style scoped>
a{
  cursor:pointer;
}
</style>

<script>
import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'BenefitsCard',
  props: {
    data: {},
  },
  mixins: [consultingStatus],
  mounted() {
    console.log('mounted');

    this.$uiHandler.swiperNextPrevShow();
  },

  computed: {
    parsedData() {
      if (this.data.list.length > 5) {
        return this.data.list.slice(0, 5);
      }

      return this.data.list;
    },
    show_more() {
      return this.data.show_more;
    },
  },
  filters: {
  },
  methods: {
    color(item) {
/*
flagHis.greenF{color:#007a34;background:#e6faee}.
flagHis.purpleF{color:#ad00c4;background:#f8eefa}.
flagHis.blueF{color:#4d6ee4;background:#e9ebfb}.
flagHis.tealF{color:#009195;background:#E1F9F9}.
flagHis.redF{color:#f42d10;background:#fff1f0}.
flagHis.brownF{color:#ae5e21;background:#ffeedf}.
flagHis.blueF02{color:#005fc2;background:#ebf6ff}.
flagHis.redF02{color:#ce0700;background:#fff1f0}.
flagHis.neonF{color:#ff8c00;background:#fdffe7}.
flagHis.grayF{color:#8b8f94;background:#ebedf2}.
flagHis.biF{color:#b69149;background:#FFF9E9}
*/

      if (this.$t('tag_color.사은') === item.eventGbnNm) {
        return 'redF';
      }
      if (this.$t('tag_color.제휴') === item.eventGbnNm) {
        return 'brownF';
      }
      if (this.$t('tag_color.세일') === item.eventGbnNm) {
        return 'blueF02';
      }
      if (this.$t('tag_color.결제') === item.eventGbnNm) {
        return 'grayF';
      }
      return 'blueF02';
    },
    click(id, item) {
      console.log(id, item);

      switch (id) {
        case '이동': {
          window.open('http://dev-pub.ssgdfs.com/mo/html/view/OMPE2.1.html');
          const tag = item.tagName;
          this.$analyticsHandler.apply(tag, { tag_cd: item.eventNo });
        }
          break;
        case '더보기': {
          window.open(this.data.more_url);
          this.$analyticsHandler.apply(this.data.tagName);
        }
          break;
        default:
          break;
      }
    },
  },
};
</script>